@font-face {
  font-family: "Yandex Sans Text";
  src: url("./fonts/YSText-Regular.eot");
  src: url("./fonts/YSText-Regular.eot?#iefix") format("embedded-opentype"),
  url("./fonts/YS Text-Regular.woff2") format("woff2"),
  url("./fonts/YS Text-Regular.woff") format("woff"),
  url("./fonts/YS Text-Regular.ttf") format("truetype"),
  url("./fonts/YS Text-Regular.svg#YSText-Regular") format("svg");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Yandex Sans Text";
  src: url("./fonts/YSText-Bold.eot");
  src: url("./fonts/YSText-Bold.eot?#iefix") format("embedded-opentype"),
  url("./fonts/YS Text-Bold.woff2") format("woff2"),
  url("./fonts/YS Text-Bold.woff") format("woff"),
  url("./fonts/YS Text-Bold.ttf") format("truetype"),
  url("./fonts/YS Text-Bold.svg#YSText-Bold") format("svg");
  font-weight: 700;
  font-style: normal;
}